.Movie {margin-bottom:50px; padding:0 20px; display:flex; width:40%; height:100%; justify-content:space-between; align-items:flex-start; flex-wrap:wrap; text-overflow:ellipsis; box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3), 0 5px 12px rgba(133, 133, 133,0.22);}
.Movie h1 {font-size:20px; font-weight:600;}
.Movie_Columns {width:30%; box-sizing:border-box; text-overflow:ellipsis;}
.Movie_Columns:last-child {padding:20px 0; width:60%;}
.Movie .Movie_Genres {display:flex; flex-wrap:wrap; margin-bottom:20px;}
.Movie_Genres .Movie_Genre {margin-right:10px; color:#333;}
.Movie .Movie_Synopsis {overflow:hidden; text-overflow:ellipsis; display:-webkit-box; -webkit-line-clamp:3; -webkit-box-orient:vertical; word-wrap:break-word; }
.Movie .Movie_Poster{ max-width:100%; position:relative; top:-20px; box-shadow:-10px 19px 38px rgba(83, 83, 83, 0.3), 10px 15px 12px rgba(80,80,80,0.22);}

@media screen and (min-width:320px) and (max-width:667px){
  .Movie {width:100%;}
}

@media screen and (min-width:320px) and (max-width:667px) and (orientation: portrait){
  .Movie {width:100%; flex-direction:column;}
  .Movie_Poster {width:100%; top:0; left:0;}
  .Movie_Columns {width:100%!important; }
}
